<template>
  <div>
    <div class="form-container" v-if="isAdmin">
      <form @submit.prevent="addProduct" class="product-form">
        <input type="text" v-model="name" placeholder="Nom du produit" class="form-input">
        <input type="file" name="image" class="form-input">
        <input type="text" v-model="price" placeholder="Prix du produit" class="form-input">
        <select v-model="category" class="form-input">
          <option disabled value="">Veuillez sélectionner une catégorie</option>
          <option>Jeans</option>
          <option>T-shirt</option>
          <option>Chaussette</option>
          <option>Chaussure</option>
        </select>
        <button type="submit" class="form-button">Ajouter le produit</button>
      </form>
    </div>

    <div ref="productsSection" class="products-container">
      <div v-for="product in products" :key="product.name" class="product-item">
        <img :src="product.image" :alt="product.name" class="product-image" @load="scrollToProduct">
        <div>{{ product.name }}</div>
        <div>{{ product.price }}</div>
        <button v-if="isLoggedIn && selectedCategory" class="add-to-collection-button" @click="addProductToMyCollection(product)">Ajouter à ma collection</button>
      </div>
    </div>

    <div class="button-container" v-if="isLoggedIn">
      <div class="category-buttons">
        <button class="category-button" @click="handleButtonClick">Ma Collection</button>
        <button class="category-button" @click="showCategories = !showCategories; showMyCollection = false; scrollToCategories(); fetchProducts(''); selectedCategory = null">Collection du Site</button>
      </div>
      <button v-if="showMyCollection" class="category-button" @click="showAddProductModal = true">
        Ajouter un produit
      </button>
      <div v-if="showCategories" ref="categoryButtons">
        <button v-for="category in filteredCategories" :key="category" class="category-button" @click="fetchProducts(category); selectedCategory = category">
          {{ category }}
        </button>
      </div>
    </div>
    <div class="modal" tabindex="-1" v-if="showAddProductModal" :class="{ 'show d-block': showAddProductModal }">
    <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Ajouter un produit</h5>
            <button type="button" class="btn-close" @click="showAddProductModal = false"></button>
          </div>
          <div class="modal-body">
            <div class="mb-3">
              <label class="form-label">Nom du produit</label>
              <input type="text" v-model="newProduct.name" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Prix du produit</label>
              <input type="number" v-model="newProduct.price" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Lien de la photo du produit</label>
              <input type="text" v-model="newProduct.image" class="form-control">
            </div>
            <div class="mb-3">
              <label class="form-label">Catégorie du produit</label>
              <select v-model="newProduct.category" class="form-control">
                <option v-for="category in categories" :key="category" :value="category">{{ category }}</option>
                <option value="new">Ajouter une nouvelle catégorie</option>
              </select>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" @click="showAddProductModal = false">Fermer</button>
            <button type="button" class="btn btn-primary" @click="addProduct">Ajouter le produit</button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { ref, nextTick, watch, computed } from 'vue';
import { useStore } from 'vuex';
import { getDatabase, ref as dbRef, set, onValue } from "firebase/database";
import { getStorage, ref as storageRef, uploadBytesResumable, getDownloadURL } from "firebase/storage";

export default {
  data() {
    return {
      showAddProductModal: false,
      newProduct: {
        name: '',
        price: '',
        image: '',
        category: ''
      }
    };
    },
  methods: {
    handleButtonClick() {
      this.showMyCollection = !this.showMyCollection;
      this.showCategories = false;
      if (this.showMyCollection) {
        this.fetchMyCollection();
      }
    },
  },
  setup() {
    const name = ref('');
    const price = ref('');
    const category = ref('');
    const categories = ref(['Jeans', 'T-shirt', 'Chaussette', 'Chaussure']);
    const products = ref([]);
    const message = ref('');
    const store = useStore();
    const isAdmin = computed(() => store.state.admin);
    const isLoggedIn = computed(() => store.state.isLoggedIn);
    const showCategories = ref(false);
    const showMyCollection = ref(false);
    const categoryButtons = ref(null);
    const userId = computed(() => store.state.user ? store.state.user.uid : null);
    const selectedCategory = ref(null);

    const scrollToCategories = () => {
      nextTick(() => {
        if (categoryButtons.value) {
          categoryButtons.value.scrollIntoView({ behavior: 'smooth' });
        }
      });
    };

    const uploadImage = async (file, userId) => {
      const storage = getStorage();
      const storageReference = storageRef(storage, `users/${userId}/products/${file.name}`);
      const uploadTask = uploadBytesResumable(storageReference, file);

      return new Promise((resolve, reject) => {
        uploadTask.on('state_changed',
            (snapshot) => {
              // Handle the upload progress
              const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              console.log('Upload is ' + progress + '% done');
            },
            (error) => {
              // Handle unsuccessful uploads
              reject(error);
            },
            () => {
              // Handle successful uploads on complete
              getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                resolve(downloadURL);
              });
            }
        );
      });
    };

    const addProduct = async (event) => {
      event.preventDefault();

      const file = event.target.elements.image.files[0];
      const imageUrl = await uploadImage(file, userId);

      const db = getDatabase();
      const productRef = dbRef(db, `users/${userId.value}/products/${category.value}/${name.value}`);
      set(productRef, {
        name: name.value,
        image: imageUrl,
        price: price.value
      });

      name.value = '';
      price.value = '';
      category.value = '';
    };

    const fetchProducts = async (category) => {
      const db = getDatabase();
      const productRef = dbRef(db, `products/${category}`);
      onValue(productRef, (snapshot) => {
        products.value = snapshot.val();
      });
    };

    const scrollToProduct = () => {
      nextTick(() => {
        const firstProductImage = document.querySelector('.product-image');
        if (firstProductImage) {
          firstProductImage.scrollIntoView({ behavior: 'smooth' });
        }
      });
    };

    watch(products, () => {
      nextTick(() => {
        const firstProductImage = document.querySelector('.product-image');
        if (firstProductImage) {
          firstProductImage.scrollIntoView({ behavior: 'smooth' });
        }
      });
    });

    const filteredCategories = computed(() => {
      return categories.value;
    });

    const fetchMyCollection = async () => {
      const db = getDatabase();
      const productRef = dbRef(db, `myCollection`);
      onValue(productRef, (snapshot) => {
        products.value = snapshot.val();
      });
    };

    const fetchSiteCollection = async () => {
      const db = getDatabase();
      const productRef = dbRef(db, `siteCollection`);
      onValue(productRef, (snapshot) => {
        products.value = snapshot.val();
      });
    };

    const addProductToMyCollection = async (product) => {
      const db = getDatabase();
      const productRef = dbRef(db, `users/${userId.value}/myCollection/${product.name}`);
      set(productRef, product);
    };

    return {fetchSiteCollection,isAdmin,name, price, category, categories, products, addProduct, fetchProducts, fetchMyCollection, addProductToMyCollection, scrollToProduct, message, filteredCategories, isLoggedIn, showCategories, showMyCollection, categoryButtons, scrollToCategories, selectedCategory};
  }
};
</script>

<style scoped>
.products-container {
  display: flex;
  flex-direction: row; /* Change this to row */
  flex-wrap: wrap;
  justify-content: center;
}

.button-container {
  display: flex;
  flex-direction: column; /* Change this to column */
  align-items: center;
  margin-top: 20px;
}

.category-button {
  justify-content: center;
  padding: 10px 20px;
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  transition: background-color 0.3s ease;
}

.category-button:hover {
  background-color: #45a049;
}

.product-image {
  max-width: 100%;
  max-height: 200px; /* Ajustez cette valeur selon vos besoins */
  object-fit: cover;
}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.product-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  padding: 20px;
  border-radius: 5px;
  background-color: #f8f9fa;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  font-size: 16px;
}

.form-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form-button:hover {
  background-color: #0056b3;
}

.products-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.product-item {
  margin: 10px;
}

.product-image {
  max-width: 100%;
  max-height: 200px;
  object-fit: cover;
}

.message {
  margin-top: 20px;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border-radius: 5px;
  transition: opacity 1s ease-out;
}

.message[hidden] {
  opacity: 0;
}

.add-to-collection-button {
  justify-content: center;
  padding: 10px 20px;
  background-color: gray; /* Changez la couleur de fond en gris */
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  transition: background-color 0.3s ease;
}

.add-to-collection-button:hover {
  background-color: darkgray; /* Changez la couleur de fond en gris foncé lors du survol */
}
</style>